export default {
	data() {
		return {
			userstock: null,
			show1: false,
		}
	},
	onLoad() {
		this.userstocks();
	},
	methods: {
		show(e) {
			console.log(e);
			uni.navigateTo({
				url: '/pages/show/show?data=' + encodeURIComponent(JSON.stringify(e))
			});
		},
		userstocks() {
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/userstocks',
				data: {
					session_key: uni.getStorageSync('storage_key')
				},
				method: 'POST',
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 200) {
						_this.userstock = res.data.data;
						_this.show1 = true;
					}
				},
				fail: (res) => {
					console.log(res.data);
				}
			});
		}
	}
}
